<template>
	<div>
		<comm-nav></comm-nav>
		<transition name="slide"><router-view></router-view></transition>
		<shopfotNav></shopfotNav>
	</div>
</template>
<script>
import shopfotNav from '@/components/shopfotNav'
import commNav from '@/components/commNav'
export default {
	data(){
		return {

		}
	},
	components:{
		shopfotNav,
		commNav,
	}
}
</script>
<style scoped>
	.anileft-transition{
	  position:absolute;
	  width:100%;
	  transition: all 3s ease;
	  overflow: hidden;
	  top:50px;
	  left:0;
	}
	.anileft-enter{
		left:100%;
	}
	.anileft-leave{
	  left:-100%;
	}
</style>



