			width:100%;height: 0.88rem;text-align: center;  position: relative;font-size:0.34rem;line-height:0.88rem; background:white;color:#404040;
		}
		nav img{
			position: absolute;top:0.26rem;left:0.32rem;width: 0.24rem;height: 0.34rem;
		}
		.receive_msg{
		  width: 100% ;border-top:0.02rem solid #f9f9f9;height:1.91rem;position: relative;
