		}
		.receive_people{
			width: 6.9rem;margin:0 auto;
			margin-top:0.36rem; position: relative;height: 0.3rem;line-height: 0.3rem;
		}
		.receive_people span:nth-of-type(1){
			display: inline-block;left:0.4rem;position: absolute;font-size:0.3rem;color:#404040;white-space: nowrap;
		}
		.receive_people span:nth-of-type(2){
			right:.2rem;position: absolute;font-size: 0.3rem;line-height: 0.3rem;height: 0.3rem;white-space: nowrap;
		}
		.receive_ad{
			width: 6.9rem;margin:0 auto;height:1.26rem;position:relative; 
		}
		.receive_ad p{
			
			width:6.45rem;position:absolute;left:0.4rem;font-size:0.3rem;top:0.2rem;
		}
		.receive_ad img{position:absolute;left:0;top:0.22rem;height :0.34rem ;width: 0.24rem

		}
		.receive_msg>img{
			position:absolute;bottom:0;width: 100%;height:0.24rem;
		}
		/*.adiamges{
			width:100%;margin:0 auto;
		}*/
		.goods_msg{
			width: 100%;height:2.68rem;border-bottom:0.02rem solid #fbfbfb;
		}
		.goods_msg>p{
			width:6.9rem;border-bottom:0.02rem solid #f9f9f9;height:0.506rem;margin:0 auto;text-align: left;font-size:0.3rem;line-height:0.506rem;  

		}
		.shoping_mack{
				width: 6.9rem;margin:0 auto;position:relative;
		}
		.shoping_mack>div{
			width:1.6rem ;height:1.6rem;border:0.02rem solid #f2f2f2;border-radius:0.12rem;position: absolute;top:0.26rem;
		}
		.shoping_mack>div>img{
