		.mark_num{
			margin-left:2.7rem;color:#404040;
		}
		.shop_express,.shop_pro,.shop_beiz{
			width:100%;height:0.83rem;border-bottom: 0.02rem solid #f9f9f9;
		}
		.shop_express>div,.shop_pro>div,.shop_beiz>div{
				width: 6.9rem;margin:0 auto;font-size:0.3rem;line-height: 0.83rem;position: relative;color:#404040;
		}
		.shop_express>div>span,.shop_pro>div>span{
			position: absolute;right:0.35rem;
		}
		.shop_express>div>span{
