		}
		.addstors span:nth-of-type(2){
			position:absolute;right:0.83rem; color:#404040;
		}
		.addstors span:nth-of-type(3){
			color:#fd888b;position: absolute;right: 0;

		}
		footer{
			position:fixed;bottom: 0;width: 100%;max-width: 640px; background:white;text-align: center;font-size:0.34rem;height: 1rem;color:black;line-height: 1rem;
		}
		footer span{
			color: #fd888b;display: inline-block;margin-left:0.1rem;
		}
		footer button{
			background:#65c4aa;font-size:0.34rem;height: 1rem;width: 2rem;float: right;color:white;right: 0.02rem;border: none;
		}
		.receive{
			background: white
		}	
</style>

				



