<template>
	<section>
		<div class="Post">
			<ul>
				<router-link tag="li" :to="{name:'detailpage',params:{id:item.id+news}}" v-for="item in data.news">
					<img :src="item.img[0]" alt="" class="img-one">
					<router-link tag="p" to="/post">{{item.tit}}<br>{{item.commemt}}</router-link>
					<router-link tag="div" to="/post" class="box">
						<img src="static/images/tx2.jpg" alt="">
						<strong></strong>
						<div class="smallbox">
							<i class="iconfont" style="background:url(static/images/s.jpg)no-repeat;background-size: 100% 100%"></i>
							<span>5</span>
						</div>
					</router-link>
				</router-link>
			</ul>
		</div>
	</section>
</template>
<script>
export default {
	data(){
		return {
			news:'news',
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText);
			},function(err){
				console.log('hh')
			})
		}
	},
	mounted(){
		this.get()
	}
}
</script>
<style scoped> 
	.wrap{background:#f9f9f9;max-width:768px;margin:0 auto;font-family:'';}
	section{margin-top:0 !important;font-size: .26rem;padding:1.7rem 0 1rem;}
	.post{margin-top:0.2rem}
	.Post ul{background:#f9f9f9;padding:.2rem .2rem;-moz-column-count:2;-webkit-column-count:2; column-count:2;-moz-column-gap:.2rem;-webkit-column-gap:.2rem;column-gap:.2rem;}
	.Post li{width:3.4rem;border:1px solid #e1e1e1;border-radius: .2rem;break-inside:avoid;
		background:white;position:relative;margin-bottom:.2rem;}
	.Post li img{width:100%;border-radius: .2rem}
	.Post .box img{width:.64rem;height:.64rem;}
	.Post li p:nth-of-type(1){padding:.10rem .08rem 0 .2rem;font-size:.26rem;}
	.Post li .box{height:.64rem;padding:.14rem .2rem;line-height: .64rem;position: relative;}
	.Post li .box .smallbox{position: absolute;right:.2rem;top:45%;transform: translateY(-50%);}
	.box strong{display: inline-block;font-size:.22rem;color:#65C4AA;font-weight:400;width:1.6rem;white-space: nowrap; overflow: hidden;text-overflow: ellipsis;}
	.Post .iconfont{display: inline-block; width:.38rem;height:.4rem;vertical-align: middle;}
	.Post span{display: inline-block;color: #404040;font-size:.22rem;}
</style>


