<template>
	<div>
		<div class="top-back">
			<img @click="back" src="static/images/goback.png">
		</div>
		<div class="photo">
		  <img src='static/images/people.png'>
			<p></p>
		</div>
		<div class="reg_content">
			<div class="regisrer">
				<img src="static/images/phone.png">
				<li>
					<span class="num">+86</span>
					<img src="static/images/stars_sjx.png">
					<input type="text" placeholder="" v-model="phototext" @blur="warn"><span class="dui" v-show="yes"></span>
				</li>
			</div>
			<p class="warning" v-show="ww"></p>
			<div class="scrod">
				<img src="static/images/lock.png">
				<li>	
					<input type="password" placeholder="" v-model="passwordtext" @keyup="err"><span class="dui" v-show="pass"></span>
				</li>
			</div>
			<div @click="aa" id="confirm" :class="{bg:phototext&&passwordtext&&aaa==1}"></div>
			<p></p>
		</div>
		<div class="reg_foot">	
			<div >
				<p></p>
			</div>
			<ul class="share">
					<li><img src="static/images/weibo.png"><p></p></li>
					<li><img src="static/images/weixin.png"><p></p></li>
					<li><img src="static/images/qq.png"><p>QQ</p></li>
			</ul>
		</div>
	</div>

</template>
<script>
	export default{
		data(){
			return{
				cartnum:[],
				data:{},
				img:['static/images/assess-user1.png','static/images/character.png','static/images/character1.png','static/images/character2.png','static/images/doyen.png','static/images/tx.jpg','static/images/tx2.jpg',],
				name:['','','','','','','','','',''],
				phototext:'',
				passwordtext:'',
				ww:false,
				yes:false,
				pass:false,
				aaa:null,
			}
		},
		methods:{
			back(){
				window.history.go(-1)
			},
			warn(){
				if (!(/^1[34578]\d{9}$/.test(this.phototext))) {
					this.phototext='';
					this.passwordtext='';
					this.ww=true
				}else{
					this.ww=false;
					if (this.phototext) {
						this.yes=true
					}else{this.yes=false}
				}
			},
			err(){
				if (!(/^.{6,20}$/.test(this.passwordtext))) {
					this.pass=false;this.aaa=null
				}else{this.pass=true;this.aaa=1}
			},
			aa(){
				if(this.phototext&&this.passwordtext){
					this.$router.push("shop")
				}
				this.data={
					name:this.name[Math.floor(Math.random()*9)],
					img:this.img[Math.floor(Math.random()*6)],
					phototext:this.phototext,
				}
				var str=JSON.stringify(this.data)
	            localStorage.setItem('key',str);
	            this.cartnum.push({
			      title:'',
			      img:'static/images/b2.jpg',
			      num:1,
			      price:19.9,
			      zo:19.9,
			      color:'',
			      check:0,
			    })
			    var str1=JSON.stringify(this.cartnum)
			    localStorage.setItem('cartnum',str1);

			},
		},
		mounted(){
			console.log(this.aaa)

		}
	}
</script>
<style scoped>
	.top-back{
		width: 100%;
	}
	.top-back img{
		margin-top:0.3rem;margin-left:0.33rem;width:0.2rem;height: 0.35rem;
	}
	.photo{
		width:100%;height: 2.2rem;text-align: center;margin-top: 0.4rem;
	}
	.photo img{
		margin:0 auto;width: 1.45rem;height: 1.45rem;
	}
	.photo p{
		font-size: 0.36rem;color: black;margin-top:0.35rem;
	}
	.regisrer{
		height:0.9rem;width:5.86rem;margin:0 auto;margin-top:0.74rem;margin-bottom:.3rem;  position: relative;
	}

	.regisrer img{
		position:absolute;top:0.3rem;left:0.08rem;width:0.3rem;height:0.38rem
	}
	.regisrer li{
		width: 5.11rem;height: 0.9rem;border-bottom: 1px solid gray;
		position: absolute;left:0.67rem;
	}
	.regisrer li>img{
		width:0.15rem;height: 0.1rem;position: absolute;left:0.6rem;top:0.4rem;
	}
	.regisrer .num{
		position:absolute;left:-0.1rem;top:0.3rem;font-size: 0.3rem;line-height: 0.34rem;color:black;
	}
	.regisrer input{
		border:none;position:absolute; top:0.3rem;font-size: 0.3rem;left:0.9rem;text-indent: 0.16rem;outline: none;
	}
	
	.scrod{
		width:5.86rem;margin:0 auto;position:relative;height: 0.9rem;}
	.scrod img{
		position:absolute;top:0.3rem;left:0.08rem;width: 0.30rem;height: 0.38rem;
	}
	.scrod li{
		position:absolute; font-size: 0.34rem;left:0.67rem;
		 height: 0.9rem;border-bottom: 1px solid gray;width:5.15rem;
		
	}
	.scrod input{
		border:none;position: absolute;left:0.14rem ;font-size: 0.3rem;top:0.3rem;
	}
	.reg_content{position: relative;}
	.reg_content>p{
		width: 5.86rem;margin:0 auto;font-size: 0.25rem;margin-top: 0.4rem;text-align:right;}
	.reg_content{
		width: 100%;
	}
	.reg_foot{
		width: 100%;margin-top: .5rem;
	}
	.reg_foot div{
		width: 5.86rem;border-top:0.02rem solid #ececec;margin:0 auto;text-align:center;
	}
	.reg_foot div p{
		display: inline-block;background:white;height:.4rem;padding:0 .2rem; font-size: 0.26rem;text-align: center;margin:0 auto;letter-spacing: 0.06rem;margin-top:-0.15rem;position: relative;top:-.2rem;}

	 #confirm{
		width: 5.86rem;height:0.78rem;background: #b2e2d5;border-radius:0.08rem;margin:0 auto;margin-top:0.57rem;font-size: 0.34rem;text-align:center;line-height:0.8rem;color:white;cursor:pointer;
	}
	.share{
		margin:0 auto;
		margin-top:0.61rem;
		width: 5.4rem;
		height:0.94rem;
	

	}
	.share li{
		width:0.75rem;padding-left:0.5rem;padding-right:0.5rem;float: left;
		}
	.share li img{
		width: 0.75rem;height: 0.75rem;
	}
	
	.share p{
		text-align:
		center;font-size: 0.25rem;margin-top:0.18rem;
	}
	.bg{background-color: #48c5a3 !important;}
	.warning{width:5.86rem !important; margin:0 auto !important;font-size: .26rem !important;color:red;text-align: left !important;padding-left:1.5rem;position: absolute;top:1rem;}
	.dui{margin-right: .2rem; position: absolute;top:50%; right:.25rem !important;transform:translateY(-50%);color:#48c5a3 !important;font-size: .32rem;font-weight: 900}
</style>


