<template>
	<div class="wrap">
		<swiper :options="swiperOption" :not-next-tick="notNextTick" ref="mySwiper">
		    <swiper-slide>
		    	<img src="static/images/01.jpg" alt="">
				<div class="mask"><p><br/>&nbsp;</p></div>
		    </swiper-slide>
		    <swiper-slide>
		    	<img src="static/images/02.jpg" alt="">
				<div class="mask"><p><br/></p></div>
		    </swiper-slide>
		    <swiper-slide>
		    	<img src="static/images/03.jpg" alt="">
				<div class="mask"><p>FEED<br/></p></div>
		    </swiper-slide>
		    <swiper-slide>
		    	<img src="static/images/04.jpg" alt="">
				<div class="mask"><p><br/></p></div>
		    </swiper-slide>
		    <div class="swiper-pagination" id="pagination" slot="pagination"></div>
		</swiper>
		<div class="box">
			<div class="form">
				<router-link to="/load" tag="button"></router-link>
				<router-link to="/register" tag="button"></router-link>
			</div>
			<p></p>
		</div>
	</div>
</template>
<script>
  export default {

    name: 'carrousel',
    data() {
      return {
        notNextTick: true,
        swiperOption: {
          // swiper optionss swiperapi
          autoplay: 3000,
          autoplayDisableOnInteraction : false,
          loop:true,
          direction : 'horizontal',
          grabCursor : true,
          setWrapperSize :true,
          pagination : '.swiper-pagination',
          paginationClickable :true,
          mousewheelControl : true,
          observeParents:true,
        }
        }
      }
    }
</script>
<style scoped>
	#pagination>.swiper-pagination-bullet-active{
		background: #fff;
	}
	.swiper-slide{
		width:100vw;
		height:100vh;
		position: relative;
		overflow: hidden;
		font-size: .4rem;
		color:#fff;
		text-align: center;
	}
	.swiper-slide img{
		width:100%;
		height: 100%;
		position: relative;
		-webkit-filter: grayscale(100%);
    	-moz-filter: grayscale(100%);
    	-ms-filter: grayscale(100%);
    	-o-filter: grayscale(100%);
    	filter: grayscale(100%);
    	filter: gray;
	}
	.mask{width:100%;height:100%;position: absolute;z-index: 99999;top:0;left:0;background-color: rgba(0,0,0,0.3);}
	.swiper-slide .mask p{
		position: absolute;
		top:50%;
		left:50%;
		transform: translate(-50%,-50%);
	}
	.swiper-pagination-bullet{
		width:.1rem !important;
		height:.1rem;
	}
	.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets{bottom:3.64rem;}
	.box{width:100%;text-align: center;position: absolute;bottom:.88rem;z-index: 999;color:#fff;max-width: 640px;}
	.box p{font-size: .26rem;color:#adadad;}
	.box .form{margin-bottom: .86rem}
	.box .form button{display: inline-block;width:2.56rem;height:.84rem;margin:0 .2rem;border:none;outline: none;border-radius: .1rem;font-size: .3rem;}
	.box .form button:nth-of-type(1){	
		background: #65c4aa;
		color:#fff;
	}
	.box .form button:nth-of-type(2){
		background: #fff;
		color:#000;
	}
</style>


