<template>
	<div>
		<nav>
			<div class="bg">
				<img src="static/images/bg.jpg">
				<div class="ghost">
					<div class="title" id="title">
						<router-link to="/fit" tag="span"><img src="static/images/install.png"></router-link>
						<span></span>
					</div>
					<div class="datum">
						<p class="over">
							<img :src="number.img">
						</p>
						<span>{{number.name}}</span>
					</div>
					<div class="message">
						<span>22</span>
						<span></span>
						<span>20</span>
						<span></span><br>
						<span></span>
					</div>
				</div>
			</div>
		</nav>
		<section>
			<div class="me">
				<span></span>
				<router-link to="/order/all" tag="span"> ></router-link>
			</div>
			<ul>
				<router-link to="/order/waitpay" tag="li">
					<img src="static/images/waitpay.png">
					<p></p>
				</router-link>
				<router-link to="/order/waitsend" tag="li" class="lis">
					<img src="static/images/waitsend.png">
					<p></p>
				</router-link>
				<router-link to="/order/sended" tag="li" class="lise">
					<img src="static/images/sended.png">
					<p></p>
				</router-link>
				<router-link to="/order/finished" tag="li">
					<img src="static/images/finished.png">
					<p></p>
				</router-link>
			</ul>
			<div class="xbox">
				<img src="static/images/shouhou.png">
				<span>/</span>
				<span>></span>
			</div>
			<router-link to="/customer" tag="div" class="xbox">
				<img src="static/images/kf.png">
				<span></span>
				<span>></span>
			</router-link>
			<router-link to="/integral" tag="div" class="xbox">
				<img src="static/images/jifen.png">
				<span></span>
				<span>></span>
			</router-link>
			<div class="xbox">
				<img src="static/images/youhui.png">
				<span></span>
				<span>></span>
			</div>
			<div class="xbox">
				<img src="static/images/shareyou.png">
				<span></span>
				<span>></span>
				<span>30</span>
			</div>
			<router-link to="/collection" tag="div" class="xbox">
				<img src="static/images/shoucang.png">
				<span></span>
				<span>></span>
			</router-link>
			<router-link to="/post" tag="div" class="xbox">
				<img src="static/images/fatie.png">
				<span></span>
				<span>></span>
			</router-link>
		</section>
		<shopfotNav></shopfotNav>
	</div>
</template>
<script>
import shopfotNav from '@/components/shopfotNav'
export default {
	data(){
		return {
			number:{},
		}
	},
	components:{
		shopfotNav,
	},
	mounted(){
		this.number=JSON.parse(localStorage.getItem("key"));
	}
}
</script>
<style scoped>
	.bg{
		height: 4.2rem;
		position: relative;
	}
	.bg img{
		width: 100%;
		height: 4.2rem;
	}
	.bg .ghost{
		color: #fff;
		position: absolute;
		top: 0;
		text-align: center;
		height: 4.2rem;
		width: 100%;
		background: rgba(0,0,0,0.4);
	}
	.bg .ghost .title{
		position: absolute;
		z-index: 1;
		width: 100%;
		height: 0.9rem;
	}
	.bg .ghost .title span:nth-child(1){
		padding-left: 0.2rem;
		margin-top: 0.2rem;
		float: left;
	}
	.bg .ghost .title span:nth-child(1) img{
		width: 0.48rem;
		height: 0.48rem;
	}
	.bg .ghost .title span:nth-child(2){
		line-height: 0.9rem;
		padding-right: 0.5rem
	}
	.bg .ghost .datum{
		margin-top: 0.75rem;
		width: 100%;
		height: 3.45rem;
		position: relative;
	}
	.datum .over{overflow: hidden;}
	.bg .ghost .datum span{
		text-align: center;
		font-size: 0.3rem;
	}
	.bg .ghost .datum p img{
		width: 1.4rem;
		height: 1.4rem;
		margin-top: 0.01rem;
		border:.04rem solid #65c4aa;
		border-radius: 50%;
	}
	.bg .ghost .message{
		position: relative;
		bottom: 1.35rem;
		left: 2.65rem;
		width: 2.3rem;
		height: 0.75rem;
	}
	.bg .ghost .message span{
		font-size: 0.25rem;
	}
	.bg .ghost .message span:nth-child(1){
		color: #65c4aa
	}
	.bg .ghost .message span:nth-child(2){
		padding-right: 0.1rem;
		border-right: 1px solid #fff;
	}
	.bg .ghost .message span:nth-child(3){
		color: #fd888b
	}
	section{
		margin: 0 0 1.1rem;
		font-size: 0.36rem;
		font-family: '';
		background: #f9f9f9;
		overflow-x: hidden;
	}
	section .me{
		width: 100%;
		height: 0.72rem;
		line-height: 0.72rem;
		background: #fff;
		border-bottom: 2px solid #e7e7e7;
	}
	section .me span{
		padding: 0.16rem 0 0 0.32rem;
		font-size: 0.26rem;
	}
	section .me span:nth-child(2){
		float: right;
		padding: 0 0.32rem 0 0;
		font-size: 0.24rem;
		color: #ababab;
	}
	section ul{
		background: #fff;
		font-size: 0;
		width: 100%;
		padding: 0 0.19rem;
		height: 1.24rem;
		border-bottom: 2px solid #e7e7e7;
	}
	section ul li{
		width: 1.78rem;
		height: 100%;
		display: inline-block;
		text-align: center;
		position: relative;
	}
	.lis:before{
		position: absolute;
	    width: 0.25rem;
	    height: 0.25rem;
	    border-radius: 50%;
	    background: #e16064;
	    text-align: center;
	    line-height: 0.25rem;
	    color: #fff;
	    right: 0.55rem;
	    top: 0.1rem;
	    font-size: 0.2rem;
	    content: '1';
	    z-index: 1;
	}
	.lise:before{
		position: absolute;
	    width: 0.25rem;
	    height: 0.25rem;
	    border-radius: 50%;
	    background: #e16064;
	    text-align: center;
	    line-height: 0.25rem;
	    color: #fff;
	    right: 0.55rem;
	    top: 0.1rem;
	    font-size: 0.2rem;
	    content: '2';
	    z-index: 1;
	}
	section ul li img{
		padding-top: 0.16rem;
		width: 0.5rem;
		height: 0.5rem;
	}
	section ul li p{
		position: absolute;
		bottom: 0.28rem;
		left: 0.48rem;
		font-size: 0.26rem;
	}
	section .xbox{
		width: 100%;
		height: 1.04rem;
		border-bottom: 1px solid #eaeaea;
		background: #fff;
		line-height: 1.04rem;
	}
	section .xbox img{
		margin-top: 0.2rem;
		margin-left: 0.25rem;
		float: left;
		width: 0.62rem;
		height: 0.6rem;
	}
	section .xbox:nth-child(4) img{
		margin-top: 0.3rem;
		margin-left: 0.3rem;
		margin-right: 0.13rem;
		width: 0.45rem;
		height: 0.45rem;
	}
	section .xbox:nth-child(7) span:nth-child(4){
		float: right;
		padding-right: 0.2rem;
		color: #feabae;
	}
	section .xbox span{
		float: left;
		font-size: 0.26rem;
	}
	section .xbox span:nth-child(3){
		color: #ababab;
		font-size: 0.3rem;
		float: right;
		margin-right: 0.3rem;
	}
	@media screen and (min-width: 769px){
		body{
			width: 640px;
			margin: 0 auto;
		}
		.bg .ghost .title{
			width: 640px;
		}
		footer{
			width: 640px;
		}
	}
</style>


