		<div class="wrap">
			<div class="header">
	            <h1><span @click="back"><img src="static/images/goback.png" alt=""></span></h1> 
	        </div>
	        <div class="section">
	            <div class="tit" style="background: url(static/images/order1.png) no-repeat;background-size: 100% 100%;">
