<template>
	<div>
		<div class="title">
			<span @click="back"><</span>
			<span></span>
		</div>
		<section>
			<div class="portrait">
				<span></span>
				<img :src="number.img">
			</div>
			<div class="xbox">
				<span></span>
				<span>></span>
			</div>
			<router-link to="/fitphone" tag="div" class="xbox">
				<span></span>
				<span>></span>
			</router-link>
			<div class="xbox">
				<span></span>
				<span>></span>
			</div>
			<div class="xbox">
				<span></span>
				<span>></span>
			</div>
			<div class="xbox">
				<span></span>
				<span>></span>
			</div>
			<div class="xbox">
				<span>/</span>
				<span>></span>
			</div>
			<div class="xbox">
				<span></span>
				<span>></span>
			</div>
		</section>
		<router-link to="/begin" tag="div" class="button"></router-link>
	</div>
</template>
<script>
export default {
	data(){
		return {
			number:{},
		}
	},
	methods:{
		back(){
			window.history.go(-1);
		}
	},
	mounted(){
		this.number=JSON.parse(localStorage.getItem("key"));
	}
}
</script>
<style scoped>
	.title{
		background: #fff;
		position: fixed;
		top: 0;
		z-index: 1;
		width: 100%;
		max-width: 640px;
		height: 0.9rem;
		text-align: center;
		line-height: 0.9rem;
		border-bottom: 2px solid #f7f7f7;
	}
	.title span:nth-child(1){
		font-size: 0.4rem;
		padding-left: .3rem;
		float: left;
	}
	section{
		background: #fff;
		margin-top: 0.9rem;
	}
	.portrait{
		width: 100%;
		height: 1.5rem;
		border-bottom: 1px solid #eaeaea;
		line-height: 1.5rem;
	}
	.portrait span{
		padding: 0 0.3rem;
		font-size: 0.3rem;
		float: left;
	}
	.portrait img{
		width: 1.16rem;
		height: 1.16rem;
		margin-top: 0.2rem;
		margin-left: 0.3rem;
	}
	.xbox{
		color: #404040;
		width: 100%;
		height: 0.8rem;
		border-bottom: 1px solid #eaeaea;
		background: #fff;
		line-height: 0.8rem;
	}
	.xbox span{
		padding: 0 0.3rem;
		font-size: 0.3rem;
		float: left;
	}
	.xbox span:nth-child(2){
		float: right;
	}
	.button{
		display: inline-block;
		width: 5.54rem;
		height: 0.84rem;
		background: #65c4aa;
		border-radius: 0.08rem;
		text-align: center;
		line-height: 0.84rem;
		color: #fff;
		position: relative;
		top: 0.26rem;
		left: 0.98rem;
	}
</style>



