<template>
	<div>
		<section>
			<div class="photo">
				<div class="title">
					<img src="static/images/soliciting.png">
					<span></span>
				</div>
				<ul>
					<router-link :to="{name:'detailpage',params:{id:item.id+miao}}" tag="li" v-for="item in data.miao"><img :src="item.img[0]"></router-link>
				</ul>
			</div>
			<router-link tag="div" :to="{name:'detailpage',params:{id:item.id+news}}" class="character" v-for="item in data.news">
				<div class="name">
					<img src="static/images/character.png">
					<span></span>
				</div>
				<div class="bg">
					<img :src="item.img[0]" class="images">
					<router-link tag="ul" to="/post">
						<li>
							<img src="static/images/fabulous.png">
							<span>{{item.zan}}</span>
						</li>
						<li>
							<img src="static/images/collection.png">
							<span>{{item.shoucang}}</span>
						</li>
						<li>
							<img src="static/images/comment.png">
							<span>{{item.xiaoxi}}</span>
						</li>
					</router-link>
					<router-link tag="p" to="/post">{{item.commemt}}</router-link>
				</div>
			</router-link> 
		</section>
	</div>
</template>
<script>
export default {
	data(){
		return {
			news:'news',
			miao:'miao',
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText);
			},function(err){
				console.log('hh')
			})
		}
	},
	mounted(){
		this.get()
	}
}
</script>
<style scoped>
	span{
		color: #65c4aa;
	}
	p{
		margin: 0.2rem 0;
	}
	section{
		margin-top:0 !important;
		font-size: .26rem;
		margin-bottom: .5rem;
		padding: 1.73rem 0.3rem 1rem 0.3rem; 
	}
	.photo{
		width: 100%;
		height: 2.55rem;
	}
	.photo .title{
		position: relative;
	}
	.photo .title img{
		height: 0.6rem;
		width: 0.6rem;
	}
	.photo .title span{
		position: absolute;
		top: .1rem;
	}
	.photo ul{
		height: 1.76rem;
		white-space: nowrap;
		overflow-x:auto;
	}
	.photo ul li{
		height:100%;
		margin-right:.2rem;
		display: inline-block;
	}
	.photo ul li img{
		width: 3.32rem;
		height: 100%;
		border-radius: .1rem;
	}
	::-webkit-scrollbar{  /**/
		display: none;
	}
	.character .name{
		margin: 0.3rem 0;
		position: relative;
	}
	.character .name img{
		height: 0.6rem;
	}
	.character .name span{
		padding-left: 0.25rem;
		position: absolute;
		top: .1rem;
	}
	.character .bg .images{
		width: 6.84rem;
		height: 6rem;
		border-radius: .2rem;
	}
	.character .bg ul{
		position: relative;
		padding-top: 0.1rem;
	}
	.character .bg ul li{
		display: inline-block;
		width: 1.28rem;
	}
	.character .bg ul li img{
		width: 0.36rem;
	}
	.character .bg ul li span{
		color: #404040;
		padding-left: 0.2rem;
		position: absolute;
		top: 0.08rem;
	}
	.bg p{overflow:hidden; text-overflow:ellipsis;display: -webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;}
	.character ol{
		width: 100%;
		overflow-y: hidden;
		white-space: nowrap;
	}
	.character ol li{
		display: inline-block;
	}
</style>


