<template>
	<div>
		<div class="wrap">
	        <div class="section">
	            <div class="title">
		            <mt-swipe :auto="4000">
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/recommend1.jpg" alt="">
					  	<div class="title-desc">
                            <p><b></b></p>
                            <p>5.2012/</p>
                            <p>RMB199.9</p>
                        </div>
					  </mt-swipe-item>
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/recommend2.jpg" alt="">
					  	<div class="title-desc">
                            <p><b></b></p>
                            <p>5.2012/</p>
                            <p>RMB199.9</p>
                        </div>
					  </mt-swipe-item>
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/recommend3.jpg" alt="">
					  	<div class="title-desc">
                            <p><b></b></p>
                            <p>5.2012/</p>
                            <p>RMB199.9</p>
                        </div>
					  </mt-swipe-item>
					</mt-swipe>
					<!-- <swiper :imgdata="imgdata"></swiper> -->
	            </div>
	            <div class="desc">
	                <span><img src="static/images/sure.png" alt=""></span><span><img src="static/images/sure.png" alt="">30</span><span><img src="static/images/sure.png" alt="">24</span>
	            </div>
	            <div class="west-life">
	                <h2></h2>
	                <div class="west-box">
	                    <router-link to="/life/denmark" tag="div" class="left">
	                        <img src="static/images/life1.png" alt="">
	                    </router-link>
	                    <div class="right">
	                        <router-link to="/life/America" tag="span"><img src="static/images/life2.png" alt=""></router-link>
	                        <router-link to="/life/french" tag="span"><img src="static/images/life3.png" alt=""></router-link>
	                    </div>
	                </div>
	            </div>
	            <div class="new-goods">
	                <h2></h2>
	                <ul>
	                    <router-link :to="{name:'detailpage',params:{id:item.id+news}}" tag="li" v-for="item in data.news">
	                        <img :src="item.img[0]" alt="">
	                        <p>{{item.tit}}</p>
	                        <span>{{item.price}}</span>
	                    </router-link>
	                </ul>
	                <p class="more"> ></p>
	            </div>
	            <div class="time">
	                <h2>.</h2>
	                <p class="time1"><img src="static/images/time.png" alt="">0523  11:00-11:30</p>
	                <ul>
	                    <router-link :to="{name:'detailpage',params:{id:item.id+miao}}" tag="li" v-for="item in data.miao">
	                        <img :src="item.img[0]" alt="" class="left">
	                        <div class="right">
	                            <p>{{item.tit}}</p>
	                            <p><span class="unline">{{item.price}}</span><span class="line">{{item.preprice}}</span></p>
	                            <span class="btn"><img src="static/images/cart.png" alt=""><em></em></span>
	                        </div>
	                    </router-link>
	                </ul>
	            </div>
	            <div class="doyen">
	                <h2></h2>
	                <router-link :to="{name:'detailpage',params:{id:item.id+daren}}" tag="li" v-for="item in data.daren">
						<h5><img src="static/images/doyen.png" alt=""><span></span></h5>
		                <img :src="item.img" alt="" class="show">
		                <p class="small">
		                    <span><img src="static/images/pointer.png" alt=""><em>{{item.zan}}</em></span>
		                    <span><img src="static/images/hearth.png" alt=""><em>{{item.shoucang}}</em></span>
		                    <span><img src="static/images/xin.png" alt=""><em>{{item.xiaoxi}}</em></span>
		                </p>
		                <p class="font">{{item.commemt}}</p>
	                </router-link>   
	            </div>
	            <div class="like">
	                <h2></h2>
	                <router-link :to="{name:'detailpage',params:{id:item.id+daren}}" tag="li" v-for="item in data.like">
	                    <img :src="item.img[0]" alt="">
	                    <h3>{{item.tit}}</h3>
	                    <p class="like-desc">{{item.recommend}}</p>
	                    <p class="price"><span>{{item.price}}</span><span></span></p>
	                </router-link>
	            </div>
	        </div>
	    </div>
	</div>
</template>
<script>
export default {
	data(){
		return {
			news:'news',
			miao:'miao',
			daren:'daren',
			like:'like',
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText);
			},function(err){
				console.log('hh')
			})
		}
	},
	mounted(){
		this.get()
	}
}
</script>
<style scoped>
	
    .section{padding-top:1.76rem;margin-bottom: 1.4rem;}
    .title{height:3.8rem;position: relative;}
    .lunbo{width:100%;height:100%;}
    .title-desc{position: absolute;padding:0 .16rem; background:rgba(255,255,255,0.8);font-size: .26rem;top:50%;transform:translate(-50%,-50%);left:50%;text-align: center;}
    .title-desc p{font-size: .23rem}
    .title-desc p:nth-of-type(1){margin-bottom:.22rem;margin-top: .38rem;}
    .title-desc p:nth-of-type(2){margin-bottom:.26rem;}
    .title-desc p:nth-of-type(3){margin-bottom:.3rem;}
    .desc{padding:0 .12rem; margin:.18rem 0;color:#6a6a6a;margin-top: -0.5rem;}
    .desc img{width:.3rem;height:.3rem;margin-right:.1rem; border-radius:50%; vertical-align: middle;}
    .desc span{display: inline-block; width:32.5%; font-size: .26rem;text-align: center;}
    .desc span:nth-of-type(3){width:35%;}
    .west-life,.new-goods,.time,.doyen,.like{margin-top:.56rem;padding:0 .32rem;}
    .west-life:after,.price:after{display: block;content: "";clear: both;}
    h2{margin-bottom: .46rem; font-size: .3rem;text-align: center;}
    .left{float:left;}
    .right{float:right;}
    .west-life .west-box .left{width:3.36rem;height:3.36rem;}
    .left img{height:100%;width:100%;}
    .west-life .right{width:3.36rem;}
    .right img{height:1.6rem;width:100%;}
    .new-goods ul,.time ul{white-space: nowrap;overflow-x:auto;}
    .new-goods ul li{display: inline-block;width:2.8rem;margin-right:.2rem; height:2.6rem;font-size: .26rem;text-align: center;}
    .new-goods ul li img{width:100%;height:100%;margin-bottom:.28rem;border-radius: .2rem;}
    .new-goods ul li p{margin-bottom:.24rem;width:2.16rem;padding:0 .32rem; white-space:nowrap; overflow:hidden; text-overflow:ellipsis;}
    .new-goods ul li span{color:#fd888b;}
    .new-goods .more{font-size: .26rem;color:#6a6a6a;text-align: center;}
    .time h2{margin-bottom:.32rem;}
    .time li{display: inline-block;}
    .time img{width:3.3rem;height:3.4rem;}
    .time .time1{margin-bottom:.32rem; font-size: .26rem;color:#6a6a6a;text-align: center;}
    .time .time1 img{width:.3rem;height:.3rem;vertical-align: middle;margin-right:.2rem;}
    .time .right{font-size: 0.26rem;text-align: center;float:left;margin:.86rem .32rem;}
    .time .right p:nth-of-type(1){margin-bottom:.38rem;width:2rem;padding:0 .15rem; overflow:hidden; text-overflow:ellipsis; }
    .time .right p:nth-of-type(2){margin-bottom:.2rem; }
    .time .right .btn{display: inline-block; width:2.3rem;height:.6rem;border:1px solid #000;line-height:.6rem;border-radius:.08rem; }
    .time .right .btn>img{width:.34rem;height:.3rem;margin-right:.16rem;vertical-align: middle;}
    .time .right .btn>em{vertical-align: middle;font-style: normal;}
    .unline{color:#fd888b;font-size: .3rem}
    .line{font-size: .16rem;text-decoration:line-through;color:#6a6a6a;}
    .cart{margin-right:.18rem;}
    .doyen{font-size: .24rem;}
    .doyen h5{color:#65c4aa;}
    .doyen h5 img{width:.54rem;height:.54rem;margin-right: .22rem; border-radius:50%;vertical-align: middle;}
    .doyen .show{width:100%;border-radius: .08rem;margin:.24rem 0;}
    .doyen .small span{margin-right: .6rem;}
    .doyen .small span img{margin-right: .12rem;vertical-align: middle}
    .doyen .small span em{vertical-align: middle;font-style:normal;}
    .doyen .small span:nth-of-type(1) img{width:.33rem;height:.36rem;}
    .doyen .small span:nth-of-type(2) img{width:.38rem;height:.34rem;}
    .doyen .small span:nth-of-type(3) img{width:.38rem;height:.34rem;}
    .doyen .font{margin-top:.24rem;font-size: .26rem;color:#6e6e6e;height:1.1rem;overflow:hidden; text-overflow:ellipsis;display: -webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical; }
    .like{font-size: .26rem;}
    .like li{margin-bottom:.66rem;}
    .like li img{width:100%;}
    .like li h3{margin:.34rem 0;}
    .like-desc{color:#6e6e6e;overflow:hidden; text-overflow:ellipsis;display: -webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;}
    .price{height:.82rem;position: relative;}
    .price span:nth-of-type(1){position: absolute;bottom:0; color:#fd888b;vertical-align: bottom;}
    .price span:nth-of-type(2){position: absolute;bottom:0; right:0;display:inline-block;height:.82rem;width:1.66rem;border:1px solid #000;border-radius:.08rem;line-height:.82rem;text-align: center;vertical-align: bottom;}
</style>


