<template>
	<div>
		<div class="wrap">
	        <div class="section">
	            <div class="title">
		            <mt-swipe :auto="4000">
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/kitchen1.jpg" alt="">
					  	<div class="title-desc">
                            <p></p>
                        </div>
					  </mt-swipe-item>
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/kitchen2.jpg" alt="">
					  	<div class="title-desc">
                            <p></p>
                        </div>
					  </mt-swipe-item>
					  <mt-swipe-item class="lb">
					  	<img class="lunbo" src="static/images/kitchen3.jpg" alt="">
					  	<div class="title-desc">
                            <p></p>
                        </div>
					  </mt-swipe-item>
					</mt-swipe>
	            </div>
	            <ul class="goods">
	                <router-link :to="{name:'detailspage',params:{id:item.id+url}}" tag="li" v-for="item in data">
	                    <img :src="item.img[0]" alt="">
	                    <p>{{item.tit}}</p>
	                    <span>{{item.price}}</span>
	                </router-link>
	            </ul>
	        </div>
	    </div>
	</div>
</template>
<script>
export default {
	data(){
		return {
			url:'kitchen',
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/kitchen.json').then(function(res){
				this.data = JSON.parse(res.bodyText).data;
			},function(err){
				console.log('hh')
			})
		}
	},
	mounted(){
		this.get()
	}
}
</script>
<style scoped>
    .section{padding-top:1.76rem;}
    .title{height:3.8rem;position: relative;}
    .lunbo{width:100%;height:100%;}
    .title-desc{position: absolute;height:.96rem;padding:0 .16rem; background:rgba(255,255,255,0.8);font-size: .26rem;top:50%;transform:translate(-50%,-50%);left:50%;line-height: .96rem;}
    .goods{text-align: center;}
    .goods li{float:left;margin:.15rem;font-size: .26rem;}
    .goods li p{width:2.8rem;padding:0 .32rem; white-space:nowrap; overflow:hidden; text-overflow:ellipsis;}
    .goods li:nth-last-of-type(1){margin-bottom:1.2rem}
    .goods li img{width:3.44rem;height:3.44rem;border-radius: .08rem;}
    .goods li span{color:#fd888b;}
</style>


