<template>
	<div class="wrap">
		<nav>
			<div class="searchk">
				<router-link to="/search" tag="span"><input type="text" name="" class="outline" placeholder=""></router-link>
				<img src="static/images/search.png" id="bg1">
				<router-link to="/message" tag="span"><img src="static/images/message.png" id="bg2"></router-link>
				<ul>
					<router-link to="/community/recommend1" tag="li"></router-link>
					<router-link to="/community/new" tag="li"></router-link>
					<router-link to="/community/decoration1" tag="li"></router-link>
					<router-link to="/community/box1" tag="li"></router-link>
					<router-link to="/community/kitchen1" tag="li"></router-link>
					<router-link to="/community/spin1" tag="li"></router-link>
				</ul>
			</div>
		</nav>
	</div>
</template>
<script>
export default{
    data(){
        return{
            inptext:'',
        }
    },
    methods:{
        search(){
            // console.log(this.$route.params)
            this.inptext='';
        }
    },
    mounted(){

    }
}
</script>
<style scoped>
	span{
		color: #65c4aa;
	}
	p{
		margin: 0.2rem 0;
	}
	nav{
		width: 100%;
		max-width: 640px;
		height: 1.65rem;
		background: #fff;
		position: fixed;
		z-index: 99;
		top: 0;		
	}
	.searchk{
		width: 100%;
		height: 100%;
		display: inline-block;
		position: relative;
	}
	.searchk input{
		font-size: 0.26rem;
		width: 5.5rem;
		height: 0.55rem;
		border-radius: 0.08rem;
		border: 0;
		background: #ebebeb;
		padding-left: 0.5rem;
		outline: none;
		position: absolute;
		top: 0.18rem;
		left: 0.45rem;
	}
	.searchk #bg1{
		height: 0.3rem;
		position: absolute;
		top: .3rem;
		left: 0.55rem;
	}
	.searchk #bg2{
		height: 0.3rem;
		width: 0.4rem;
		position: absolute;
		top: .3rem;
		right: 0.4rem;
	}
	.send{
		color:#999;
		height: 0.3rem;
		position: absolute;
		top: .3rem;
		right: 0.3rem;
	}
	.searchk ul{
		width: 96%;
		font-size: 0;
		color: #999999;
		margin: 0.8rem auto;
	}
	.searchk ul li{
		font-size: 0.28rem;
		display: inline-block;
		padding: 0.2rem 0 0.2rem;
		width: 16.6%;
		text-align: center;
	}
	.active{
		color: #65c4aa;
		border-bottom: .08rem solid #65c4aa;
	}
</style>


