<template>
	<section>
		<div class="character" v-for="item in data">
			<div class="name">
				<img src="static/images/character.png">
				<span></span>
			</div>
			<router-link tag="div" :to="{name:'detailspage',params:{id:item.id+url}}" class="bg">
				<img :src="item.img[0]" class="images">
				<router-link tag="ul" to="/post">
					<li>
						<img src="static/images/fabulous.png">
						<span>{{item.zan}}</span>
					</li>
					<li>
						<img src="static/images/collection.png">
						<span>{{item.shoucang}}</span>
					</li>
					<li>
						<img src="static/images/comment.png">
						<span>{{item.xiaoxi}}</span>
					</li>
				</router-link>
				<router-link tag="p" to="/post" class="comment">{{item.commemt}}</router-link>
			</router-link>
		</div>
	</section>
</template>
<script>
export default {
	data(){
		return {
			url:'box',
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/box.json').then(function(res){
				this.data = JSON.parse(res.bodyText).data;
			},function(err){
				console.log('hh')
			})
		}
	},
	mounted(){
		this.get()
	}
}
</script>
<style scoped>
	span{
		color: #65c4aa;
	}
	p{
		margin: 0.2rem 0;
	}
	section{
		margin-top:0 !important;
		font-size: .26rem;
		padding: 1.65rem 0.3rem 1rem 0.3rem; 
	}
	.character .name{
		border-top: 0.08rem solid #f9f9f9;
		position: relative;
	}
	.character .name img{
		height: 0.6rem;
	}
	.character .name span{
		padding-left: 0.25rem;
		position: absolute;
		top: .1rem;
	}
	.character .bg .images{
		width: 6.84rem;
		height: 6rem;
	}
	.character .bg ul{
		position: relative;
		padding-top: 0.1rem
	}
	.character .bg ul li{
		display: inline-block;
		width: 1.28rem;
	}
	.character .bg ul li img{
		width: 0.36rem;
	}
	.character .bg ul li span{
		color: #404040;
		padding-left: 0.2rem;
		position: absolute;
		top: 0.08rem;
	}
	.comment{
		overflow: hidden;
	    text-overflow: ellipsis;
	    display: -webkit-box;
	    -webkit-line-clamp: 2;
	    -webkit-box-orient: vertical;
	}
</style>


