<template>
	<div>
		<div class="wrap">
			<div class="header">
	            <h1><span class="back" @click="back"><img src="static/images/t.png" alt=""></span><span><img src="static/images/share.png" alt=""><em></em></span></h1> 
	        </div>
	        <div class="section">
	            <div class="title">
	                <mt-swipe :auto="3000">
					  <mt-swipe-item class="lb" v-for="item in data.img">
					  	<img class="lunbo" :src="item" alt="">
					  </mt-swipe-item>
					</mt-swipe>
	            </div>
	            <div class="goods">
	                <h2>{{data.tit}}</h2>
	                <p class="msg"><span> {{data.price}}</span><span>{{data.classify}}</span></p>
	                <div class="desc">
	                    <span><img src="static/images/sure.png" alt=""></span><span><img src="static/images/sure.png" alt="">30</span><span><img src="static/images/sure.png" alt="">24</span>
	                </div>
	            </div>
	            <div class="sum">
	                <span></span>
	                <span>+</span>
	            </div>
	            <div class="cmd">
	                <h3></h3>
	                <p>{{data.recommend}}</p>
	            </div>
	            <div class="assess">
	                <div class="sum">
	                    <span> 4</span>
	                    <span>></span>
	                </div>
	                <ul>
	                    <li v-for="item in data.img">
	                        <div class="img"><img :src="item" alt=""></div>
	                        <p><span></span><i></i></p>
	                        <p><img src="static/images/assess-user1.png" alt=""><span></span></p>
	                    </li>
	                    <li v-for="item in data.img">
	                        <div class="img"><img :src="item" alt=""></div>
	                        <p><span></span><i></i></p>
	                        <p><img src="static/images/assess-user1.png" alt=""><span></span></p>
	                    </li>
	                </ul>
	            </div>
	            <div class="goods-details">
	                <h3></h3>
	                <div class="style">
	                    <p><b></b><span></span></p>
	                    <p><b></b><span>3.9cm,10.5cm,12.6cm</span></p>
	                    <p><b></b><span></span></p>
	                    <p><b></b><span></span></p>
	                    <p><b></b><span>375ml</span></p>
	                    <p><b></b><span>255g</span></p>
	                </div>
	                <div class="goods-desc">
	                    <h2>WAVY MUG</h2>
	                    <img :src="data.img[2]" alt="" class="goods-descimg">
	                    <h2>MAIN MESSAGES</h2>
	                    <img src="static/images/bo.png" alt="" class="bo">
	                    <p class="font">{{data.recommend}}</p>
	                    <img :src="data.img[1]" alt="" class="goods-descimg">
	                    <div class="goods-descbox">
	                        <b>/</b><span></span>
	                    </div>
	                    <img :src="data.img[0]" alt="" class="goods-descimg">
	                    <div class="goods-descbox">
	                        <b>/</b><span></span>
	                    </div>
	                    <img :src="data.img[2]" alt="" class="goods-descimg">
	                    <ul>
	                        <li>
	                            <h4></h4>
	                            <p></p>
	                        </li>
	                         <li>
	                            <h4></h4>
	                            <p></p>
	                        </li>
	                         <li>
	                            <h4></h4>
	                            <p></p>
	                        </li>
	                    </ul>
	                </div>   
	            </div>
	            <div class="know">
	                <div class="img"><img src="static/images/about.png" alt=""></div>
	                <h2>SHOPPING INFORMATION</h2>
	                <h3>  |  |  | </h3>
	                <dl>
	                    <dt></dt>
	                    <dd>888810</dd>
	                </dl>
	                <dl>
	                    <dt></dt>
	                    <dd>1.30</dd>
	                    <dd>2.</dd>
	                </dl>
	                <dl>
	                    <dt></dt>
	                    <dd>1.888810</dd>
	                    <dd>2.40-1236545-991</dd>
	                </dl>
	            </div>
	        </div>
	        <div class="footer">
	            <ul>
	                <router-link to="/customer" tag="li"><img src="static/images/title.png" alt=""></router-link>
	                <router-link tag="li" to="/cart"><img src="static/images/cart.png" alt=""></router-link>
	                <li @click="addcart"></li>
	                <router-link to="/confirm" tag="li"></router-link>
	            </ul>
	        </div>
		    <mt-popup
			  v-model="popupVisible"
			  :modal="false"
			  popup-transition="popup-fade"
			  position="bottom">
			  <div class="add" v-show="true">
		        <h2><img src="static/images/delete.png" alt="" @click="none"></h2>
		        <ul class="cart-box">
		            <li><img :src="data.img[0]" alt=""></li>
		            <li class="cart-detail">
		                <p class="price">$ {{parseFloat(data.price*num).toFixed(1)}}</p>
		                <p></p>
		                <p class="num"><span @click="reduce">-</span><span>{{num}}</span><span @click="add">+</span></p>
		            </li>
		        </ul>
		        <h3></h3>
		        <ul class="color">
		            <li v-for="(item,index) in color" @click="coll(index)" :class="{bgbg:i==index}">{{item}}</li>
		        </ul>
		        <div class="sure" @click="set"></div>
		    </div>
			</mt-popup>
	    </div>
	</div>
</template>
<script>
export default {
	name:'detailpage',
	data(){
		return{
			cartnum:[],
			num:1,
			zo:null,
			i:null,
			color:['','','',''],
			popupVisible:false,
			data:'',
			id:null,
			url:'',
			col:false,
		}
	},
	methods:{
		back(){
			window.history.go(-1)
		},
		addcart(){
			this.popupVisible=true
		},
		none(){
			this.popupVisible=false
		},
		set(){
			this.cartnum=JSON.parse(localStorage.getItem("cartnum"));
			this.popupVisible=false
			if (this.i>-1) {
				this.cartnum.push({
					title:this.data.tit,
					img:this.data.img[0],
					num:this.num,
					price:this.data.price,
					zo:parseFloat(this.data.price*this.num).toFixed(1),
					color:this.color[this.i],
					check:0,
				})
				var str=JSON.stringify(this.cartnum)
		        localStorage.setItem('cartnum',str);
		        this.$toast({
				  message: '',
				  position: 'middle',
				  duration: 2000,
				});
			}
		},
		coll(inde){
			this.col=true;
			this.i=inde;
		},
		add(){
	      this.num++;
	      this.zo=this.price*this.num;
	    },
	    reduce(){
	      if (this.num>1) {
	          this.num--;
	          this.zo=this.price*this.num;
	      }
	    },
	},
	mounted(){

		this.id=parseInt(this.$route.params.id.substring(0,1));
		var aa=this.$route.params.id.substring(1);
		if (aa=='news') {
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText).news[this.id];
				console.log(this.data)
			},function(err){
				console.log('hh')
			})
		}
		if (aa=="miao") {
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText).miao[this.id];
				console.log(this.data)
			},function(err){
				console.log('hh')
			})
		}
		if (aa=="like") {
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText).like[this.id];
				console.log(this.data)
			},function(err){
				console.log('hh')
			})
		}
		if (aa=="daren") {
			this.$http.get('static/data/recommend.json').then(function(res){
				this.data = JSON.parse(res.bodyText).daren[this.id];
				console.log(this.data)
			},function(err){
				console.log('hh')
			})
		}
	},
}
</script>
<style scoped>
	.mint-popup-bottom{width:100%;margin:0 auto;}
    .wrap{background-color: #f9f9f9;}
	li{list-style: none;}
    .header{width:100%;max-width: 640px; height:.88rem;line-height: .88rem;position:absolute;top:0;z-index: 9999;}
    .header h1{text-align: center;font-weight: 400;width:100%;}
    .header h1 span:nth-of-type(1){width:.64rem;height:.64rem; position: absolute;left:.12rem; top:50%;transform:translateY(-50%); border-radius: 50%;line-height: .64rem;font-size: .46rem;}
    .header h1 span:nth-of-type(1)>img{width:100%;height:100%;}
    .header h1 span:nth-of-type(2){width:2.3rem;height:.64rem; position: absolute;top:50%;right:.32rem; transform:translateY(-50%);border-radius:.34rem;background: #8b8b8b;line-height: .64rem;color:#d0d0d0;font-size: .26rem;}
    .header h1 span:nth-of-type(2)>img{width:.32rem;height:.32rem;margin-right:.08rem; vertical-align: middle;}
    .header h1 span:nth-of-type(2)>em{font-style: normal; vertical-align: middle;}
    .section{margin-bottom:1rem;}
    .title{height:5rem;position: relative;}
    .lunbo{width:100%;height:100%;}
    .swiper-container{width:100%;height:5.24rem;}
    .swiper-slide{position: relative;text-align: center;width:100%;}
    .swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{bottom:0;}
    .swiper-pagination-bullet-active{background: #666;}
    .swiper-pagination-bullet{background: #dcdcdc;}
    .goods{margin-top:.1rem;background:#fff;font-size: .26rem;}
    .goods h2{padding:0 .32rem; height:.68rem;line-height: .68rem; font-weight:400;font-size: .32rem;}
    .goods .msg{padding:0 .32rem; height:.68rem;line-height: .68rem;}
    .goods .msg span:nth-of-type(1){margin-right:.22rem;color:#fd888b;}
    .goods .msg span:nth-of-type(2){padding:.1rem .12rem;border:1px solid #979797;border-radius:.08rem;color:#979797;}
    .desc{padding:0 .12rem; margin:.18rem 0;color:#6a6a6a;}
    .desc img{width:.3rem;height:.3rem;margin-right:.1rem; border-radius:50%; vertical-align: middle;}
    .desc span{display: inline-block; width:32.5%; font-size: .26rem;text-align: center;}
    .desc span:nth-of-type(3){width:35%;}
    .sum{height:.68rem; margin:.03rem;padding:0 .32rem;line-height:.68rem;font-size: .26rem;background: #fff;}
    .sum span:nth-of-type(1){color:#6a6a6a;}
    .sum span:nth-of-type(2){float:right;font-size: .46rem;font-weight:900;color:#6a6a6a;}
    .cmd{padding:0 .32rem; margin-bottom:.1rem; font-size: .26rem;background: #fff;overflow: hidden;}
    .cmd h3 .goods-details h3{margin:.18rem 0;font-weight:400;font-size:.26rem;}
    .goods-details h3{text-align: center;font-size: .36rem;}
    .cmd p{padding:.2rem 0;color:#6a6a6a;}
    .assess ul{padding:0 .32rem;white-space:nowrap;overflow-x: auto;background: #fff;font-size: .26rem;}
    .assess ul li{display: inline-block;margin-right:.24rem;}
    .assess ul li .img{width:2.6rem;height:2.45rem;padding: .27rem .1rem;box-shadow:0 .05rem .05rem #eee;}
    .assess ul li .img>img{width:100%;height:100%;}
    .assess ul li p:nth-of-type(1){margin-top:.26rem;}
    .assess ul li p:nth-of-type(2){margin:.18rem 0;color:#65c4aa;}
    .assess ul li p:nth-of-type(2)>img{height:.5rem;width:.5rem;margin-right:.1rem; border-radius: 50%;vertical-align: middle;}
    .goods-details{margin:.1rem 0; background:#fff;font-size: .22rem;font-weight:40;}
    .goods-details h3{text-indent:.32rem; padding:.18rem;box-shadow:0 .1rem .1rem #ececec;}
    .goods-details .style{padding:0 .32rem;margin:.1rem 0;line-height: .56rem;border-bottom:.1rem solid #ececec;}
    .goods-details .style b{margin-right:.8rem;font-weight:400;}
    .goods-details .style p{border-bottom:.02rem solid #ececec;}
    .goods-details .style p:nth-last-of-type(1){border-bottom:none;}
    .goods-details .style span{color:#959595;}
    .goods-desc{text-align: center;font-size: .26rem;}
    .goods-desc h2{margin-top:1.7rem;margin-bottom:.4rem;}
    .goods-desc .goods-descimg{width:80%;height:3.4rem;margin-top:.5rem;}
    .goods-desc .bo{width:1rem;height:.2rem;}
    .font{padding:.4rem .64rem;font-size: .24rem;color:#8f8f8f;}
    .goods-descbox{padding:.2rem .53rem .3rem .32rem;margin-top:.8rem; background: #f9f9f9;}
    .goods-descbox b{font-size: .46rem;}
    .goods-descbox span{font-size: .24rem;position: relative;top:.1rem;}
    .goods-desc ul{text-align: left;padding:0 .8rem;margin-top:.8rem;}
    .goods-desc ul li{border-bottom:.02rem solid #ececec;}
    .goods-desc ul li>h4{display:inline-block;padding:0 .2rem;margin:.2rem 0; line-height:.44rem;background: #65c4aa;color:#fff;border-radius: .04rem}
    .goods-desc ul li>p{margin-bottom: .4rem;}
    .know{font-size: .26rem;}
    .know>h2,.know>h3,.know>.img{text-align: center;}
    .know>.img{margin:.24rem 0;}
    .know>h2{margin:.1rem 0;font-size: .32rem}
    .know>h3{font-size: .32rem}
    .know>dl{padding:0 .3rem;margin-bottom:.4rem;}
    .know>dl>dt{list-style-type:disc;margin-bottom: .2rem;}
    .know>dl>dd{color:#a2a2a2;}

    /*footer css start*/
    .footer{width:100%;max-width: 640px; background: #fff;position: fixed;bottom:0;}
    .footer ul{padding:0 .32rem;}
    .footer ul li{height:1rem; float:left;font-size: .26rem;}
    .footer ul li:nth-of-type(1){height:.6rem;width:.6rem;padding:.2rem; border-right:.02rem solid #ececec;}
    .footer ul li:nth-of-type(1) img{height:100%;width:100%;}
    .footer ul li:nth-of-type(2){height:.6rem;width:.6rem;padding:.2rem;}
    .footer ul li:nth-of-type(2) img{height:100%;width:100%;}
    .footer ul li:nth-of-type(3){height:.68rem;width:2rem;margin-top: .16rem;text-align: center;float:right;line-height: .64rem;background: #65c4aa;color:#fff;border-radius:.08rem;}
    .footer ul li:nth-of-type(4){height:.64rem;width:1.96rem;margin-right:.22rem;margin-top: .16rem; border:1px solid #000 !important;text-align: center;float:right;line-height: .64rem;border-radius:.08rem;}

    .add{width:100vw;max-width: 640px;position:fixed;bottom:0;left:50%;transform: translateX(-50%); background:#fff;font-size:.26rem;z-index: 9999;}
    .add h2{height:1.2rem;padding:0 .23rem;border-bottom:.02rem solid #eaeaea; line-height: 1.2rem;font-weight:400;font-size:.3rem;position: relative;}
    .add h2 img{width:.32rem;height:.32rem;position: absolute;top:50%;right:.32rem;transform:translateY(-50%);}
    .cart-box{height:2rem; position: relative;background: #fff;padding:0 .34rem;margin: .4rem 0;}
    .cart-box li{display: inline-block;font-size: .26rem}
    .cart-box li img{width:1.8rem;height:1.8rem;border:.02rem solid #f9f9f9;border-radius:0.06rem;position:absolute;top:50%;transform: translateY(-50%);border:1px solid #f9f9f9;border-radius:0.06rem;box-shadow: 0 .01rem .03rem #999;}
    .cart-box .cart-detail{position: absolute;left:2.5rem;padding-top:.2rem;}
    .cart-detail p{margin-bottom:.18rem;}
    .cart-detail .price{color:#fd888b;}
    .cart-detail .num{display: inline-block; border-radius:.06rem;border:.02rem solid #8b8b8b;}
    .cart-detail .num span{display: inline-block;width:.62rem;height:.62rem;line-height: .62rem;text-align: center;}
    .cart-detail .num span:nth-of-type(2){border-left:.02rem solid #8b8b8b;border-right:.02rem solid #8b8b8b;}
    .add h3{padding:0 .32rem;margin-bottom: .3rem; font-weight: 400;color:#6a6a6a;}
    .add .color{padding:0 .32rem;}
    .add .color:after{display: block;content: '';clear:both;}
    .add .color li{float:left; width:2rem;height:.8rem;margin:0 .32rem .24rem 0; border:.02rem solid #8b8b8b;border-radius:.08rem; line-height:.8rem;text-align:center;font-size: .3rem;}
    .add .color li:nth-of-type(3n+0){margin-right: 0;}
    .sure{height:1rem;background: #48c5a3;line-height: 1rem;text-align: center;color:#fff;font-size: .36rem;}
    .bgbg{background:#48c5a3 !important;color:#fff; }
</style>


