<template>
	<div>
		<div class="footer">
            <ul class="nav-foot">
                <li>
                    <router-link to="/shop" tag="i"><span class="font"></span></router-link> 
                </li>
                <li>
                    <router-link to="/community" tag="i"><span class="font"></span></router-link>
                </li>
                <li>
                    <router-link to="/cart" tag="i"><span class="font"></span></router-link>
                    <!-- <span class="num">{{length}}</span> -->
                </li>
                <li>
                    <router-link to="/userinfo" tag="i"><span class="font"></span></router-link>
                </li>
            </ul>
        </div>
	</div>
</template>
<script>
export default{
    data(){
        return{
            cartnum:[],
            length:null,
        }
    },
    mounted(){
        this.cartnum=JSON.parse(localStorage.getItem("cartnum"));
        this.length=this.cartnum.length
    }
}
</script>
<style scoped>
    .footer{position: fixed;bottom:0;width:100%;background: #fff;max-width: 640px}
    .footer ul li{float: left;width:25%;text-align:center;font-size:.26rem;margin:.1rem 0;position: relative;}
    .font{width:100%; position: absolute;top:.6rem;left:0;}
    .footer ul li i{display: inline-block;margin-bottom:.3rem; width:.6rem;height:.6rem;background:url(../../static/images/icon/icon.png) no-repeat;background-size:2.4rem .6rem;font-style: normal; }
    .footer ul li:nth-of-type(1) i{background-position:0 0; }
    .footer ul li:nth-of-type(2) i{background-position:-.6rem 0 !important; }
    .footer ul li:nth-of-type(3) i{background-position:-1.2rem 0 !important;}
    .footer ul li:nth-of-type(4) i{background-position:-1.8rem 0 !important; }
    .num{width:.3rem;height:.3rem; position: absolute;background: #fd888b;color:#fff;text-align: center;line-height: .3rem;border-radius: 50%;top:-.05rem;right:.54rem;}
    .active{color:#65c4aa;background:url(../../static/images/icon/icon-active.png) no-repeat !important;background-size:2.4rem .6rem !important;}
</style>


