import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/shopfotNav'
import Shop from '@/pages/shop'

import Orderd from '@/pages/order-d'

import Recommend from '@/pages/recommend'
import Recommend1 from '@/pages/recommend1'
import Spin from '@/pages/spin'
import Spin1 from '@/pages/spin1'
import Kitchen from '@/pages/kitchen'
import Kitchen1 from '@/pages/kitchen1'
import Box from '@/pages/box'
import Box1 from '@/pages/box1'
import Decoration from '@/pages/decoration'
import Decoration1 from '@/pages/decoration1'
import New from '@/pages/new'
import life from '@/pages/life'

import Detailspage from '@/pages/details-page'
import Detailpage from '@/pages/detail-page'

import Community from '@/pages/community'
import Cart from '@/pages/cart'

import Userinfo from '@/pages/userinfo'
import collectionother from '@/pages/collectionother'
import homeother from '@/pages/homeother'
import paysuccess from '@/pages/paysuccess'
import message from '@/pages/message'
import post from '@/pages/post'
import fit from '@/pages/fit'
import fitphone from '@/pages/fitphone'
import postedit from '@/pages/postedit'
import customer from '@/pages/customer'

import begin from '@/pages/begin'
import load from '@/pages/load'
import register from '@/pages/register'

import order from '@/pages/order'
import all from '@/components/all'
import waitpay from '@/components/wait-pay'
import waitsend from '@/components/wait-send'
import sended from '@/components/sended'
import finished from '@/components/finished'

import Search from '@/pages/search'
import Searchabout from '@/pages/search-about'
import Searchsingle from '@/components/search-single'
import Searchpost from '@/components/search-post'
import Searchuser from '@/components/search-user'


import Integral from '@/pages/integral'
import collection from '@/pages/collection'
import exchange from '@/components/exchange'
import exhistory from '@/components/exhistory'
import exmore from '@/components/exmore'

import confirm from '@/pages/confirm'


import swiper from '@/components/swiper'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  // scrollBehavior (to, from, savedPosition) {
  //   return { x: 0, y: 0 }
  // },
  linkActiveClass:'active',
  routes: [
    { path: "*", redirect: '/begin' },
    { path: '/begin', component: begin },
    { path: '/load', component: load },
    { path: '/register', component: register },
    {
      path: '/shop',
      component: Shop,
      children: [
        { path: '', component: Recommend},
        { path: 'recommend', component: Recommend },
        { path: 'decoration', component: Decoration },
        { path: 'spin', component: Spin },
        { path: 'box', component: Box },
        { path: 'kitchen', component: Kitchen },
        
      ]
    },
    {
      path: '/community',
      component: Community,
      children: [
        { path: '', component: Recommend1},
        { path: 'recommend1', component: Recommend1 },
        { path: 'new', component: New },
        { path: 'decoration1', component: Decoration1 },
        { path: 'spin1', component: Spin1 },
        { path: 'box1', component: Box1 },
        { path: 'kitchen1', component: Kitchen1 },
        
      ]
    },
    { path: '/cart',component: Cart},
    {path: '/userinfo',component: Userinfo},
    {path: '/collection',component: collection},
    {
      path: '/integral',
      component: Integral,
      children: [
        { path: '', component: exchange},
        { path: 'exchange', component: exchange },
        { path: 'exhistory', component: exhistory },
        { path: 'exmore', component: exmore },
      ]
    },
    {
      path: '/order',
      component: order,
      children: [
        { path: '', component: all},
        { path: 'all', component: all},
        { path: 'waitpay', component: waitpay},
        { path: 'waitsend', component: waitsend},
        { path: 'sended', component: sended},
        { path: 'finished', component: finished},
        
      ]
    },
    {
      path: '/swiper',
      component: swiper
    },
    {
      path: '/detailspage/:id',
      name:'detailspage',
      component: Detailspage
    },
    {
      path: '/detailpage/:id',
      name:'detailpage',
      component: Detailpage
    },
    {
      path: '/search',
      component: Search
    },
    {
      path: '/search-about',
      component: Searchabout,
      children:[
        {path:'',component:Searchsingle},
        {path:'single',component:Searchsingle},
        {path:'post',component:Searchpost},
        {path:'user',component:Searchuser},
      ]
    },
    {path:'/confirm',component:confirm},
    {path:'/order-details',component:Orderd},
    {path:'/life/:id',component:life},
    {path:'/collectionother',component:collectionother},
    {path:'/homeother',component:homeother},
    {path:'/paysuccess',component:paysuccess},
    {path:'/message',component:message},
    {path:'/post',component:post},
    {path:'/fit',component:fit},
    {path:'/fitphone',component:fitphone},
    {path:'/postedit',component:postedit},
    {path:'/customer',component:customer},

  ]
})



