<template>
	<div>
		<div class="title">
			<span @click="back"><</span>
			<span></span>
		</div>
		<section>
			<div class="replace">
				<img :src="number.img">
				<p>{{number.phototext}}</p>
				<router-link to="" tag="div" class="button"></router-link>
			</div>
		</section>
	</div>
</template>
<script>
export default {
	data(){
		return {
			number:{}
		}
	},
	methods:{
		back(){
			window.history.go(-1);
		}
	},
	mounted(){
		this.number=JSON.parse(localStorage.getItem("key"));
	}
}
</script>
<style scoped>
	.title{
		background: #fff;
		position: fixed;
		top: 0;
		z-index: 1;
		width: 100%;
		height: 0.9rem;
		text-align: center;
		line-height: 0.9rem;
		border-bottom: 2px solid #f7f7f7;
	}
	.title span:nth-child(1){
		font-size: 0.4rem;
		padding-left: .3rem;
		float: left;
	}
	section{
		margin: 1.62rem 0 0;
	}
	.replace{
		height: 4.16rem;
		width: 100%;
		text-align: center;
		line-height: 1.02rem;
	}
	.replace img{
		width: 1.16rem;
		height: 1.16rem;
	}
	.replace p{
		margin-top: 0.3rem;
		font-size: 0.34rem;
		color: #404040;
	}
	.replace .button{
		margin-top: 0.22rem;
		display: inline-block;
		width: 5.54rem;
		height: 0.84rem;
		background: #65c4aa;
		border-radius: 0.08rem;
		text-align: center;
		line-height: 0.84rem;
		color: #fff;
	}
	@media screen and (min-width: 769px){
        .title{
          width: 640px;
        }
    }
</style>


