<template>
	<div class="wrap">
		<nav>
			<span @click="back"><</span>
			<span></span>
		</nav>
		<section>
			<div class="box">
				<img src="static/images/kf1.png">
				<router-link to="/customer" class="xb" tag="div"> 
					<span></span><br>
					<span></span>
					<span><img src="static/images/jtt.png"></span>
				</router-link>
			</div>
			<div class="box lise">
				<img src="static/images/jinxuan.png">
				<div class="xb">
					<span></span><br>
					<span>20</span>
					<span><img src="static/images/jtt.png"></span>
				</div>
			</div>
			<div class="box">
				<img src="static/images/zan.png">
				<div class="xb">
					<span></span><br>
					<span></span>
					<span><img src="static/images/jtt.png"></span>
				</div>
			</div>
			<div class="box">
				<img src="static/images/pinlun.png">
				<div class="xb">
					<span></span><br>
					<span>... ...</span>
					<span><img src="static/images/jtt.png"></span>
				</div>
			</div>
			<div class="box">
				<img src="static/images/xitong.png">
				<div class="xb">
					<span></span><br>
					<span></span>
					<span><img src="static/images/jtt.png"></span>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
export default {
	data(){
		return{

		}
	},
	methods:{
		back(){
			window.history.go(-1)
		}
	}
}
</script>
<style scoped>
	.wrap{
		margin: 0;
		padding: 0;
		font-size: 0.32rem;
	}
	nav{
		background: #fff;
		position: fixed;
		top: 0;
		z-index: 1;
		width: 100%;
		height: 0.9rem;
		text-align: center;
		line-height: 0.9rem;
		border-bottom: 1px solid #f7f7f7;
	}
	nav span:nth-child(1){
		font-size: 0.4rem;
		padding-left: .3rem;
		float: left;
	}
	nav span:nth-child(2){
		padding-right: 0.3rem;
	}
	section{
		margin: 1.1rem 0 0;
	}
	section .box{
		position: relative;
		margin-top: 0.2rem;
		width: 100%;
		height: 1.34rem;
		background: #fff;
	}
	section .box img{
		width: 0.89rem;
		height: 0.89rem;
		margin-top: 0.2rem;
		margin-left: 0.26rem;
	}
	section .box .xb{
		position: absolute;
		top: 0.25rem;
		width: 6rem;
		display: inline-block;
	}
	section .box .xb span{
		float: left;
		font-size: 0.29rem;
		padding-left: 0.3rem;
	}
	section .box .xb span:nth-child(3){
		white-space:nowrap; 
		overflow:hidden; 
		text-overflow:ellipsis;
		width: 4.44rem;
		padding-top: 0.12rem;
		font-size: 0.26rem;
		color: #8c8c8c;
	}
	section .box .xb span:nth-child(4){
		float: right;
		margin-top: -0.3rem;
	}
	section .box .xb span:nth-child(4) img{
		width: 0.2rem;
		height: 0.34rem;
	}
	.lise:before{
		position: absolute;
	    width: 0.35rem;
	    height: 0.35rem;
	    border-radius: 50%;
	    background: #e16064;
	    color: #fff;
	    right: 0.65rem;
	    top: 0.55rem;
	    text-align: center;
	    line-height: 0.35rem;
	    font-size: 0.2rem;
	    content: '2';
	    z-index: 1;
	}
	@media screen and (min-width: 769px){
		body{
			width: 640px;
			margin: 0 auto;
		}
		nav{
			width: 640px;
		}
	}
</style>


