<template>
	<div class="wrap">
    <header>
      <div>
        <button class="goback" @click="back"><img src="static/images/goback.png"></button>
        <p>{{data.title}}</p>
      </div>
    </header>
    <section>
      <div class="denmark-div"><img src="static/images/p1.png"></div>
      <div class="denmarklive">
        <h3>{{data.title}}</h3>
        <p>{{data.details}}</p>
      </div>
      <ul class="denmark-ul">
        <router-link :to="{name:'detailspage',params:{id:item.id+url}}" tag="li" v-for="item in data.data">
          <img :src="item.img[0]">
          <p>{{item.tit}}</p>
          <span>{{item.price}}</span>
        </router-link>
      </ul>
    </section>
  </div>
</template>
<script>
export default {
	data(){
		return {
			url:this.$route.params.id,
			data:[],
		}
	},
	methods:{
		get(){
			this.$http.get('static/data/'+this.$route.params.id+'.json').then(function(res){
				this.data = JSON.parse(res.bodyText);
			},function(err){
				console.log('hh')
			})
		},
		back(){
			window.history.go(-1)
		}
	},
	mounted(){
		this.get();

	}
}
</script>
<style scoped>
	  .wrap{
	      width: 7.5rem;
	      margin: 0 auto;
	      overflow: hidden;
	  }
	  li{
	      list-style: none;
	  }
	header{
	    position: fixed;
	    border-bottom: 1px solid #fbfbfb;
	    width: 7.5rem;
	    height: 0.95rem;
	    z-index: 666;
	}
	header div{
	  width: 100%;
	  height: 0.95rem;
	  margin: 0;
	  position: relative;
	  background-color: #fff;
	}
	header div button{
	    border: none;
	    outline-style: none;
	    background: transparent;
	    width: 0.2rem;
	    height:0.35rem;
	    position: absolute;
	    top: 0.35rem;
	    left: 0.3rem;   
	}
	.goback img{
	  width: 100%;
	  height: 100%;
	}
	header div p{
	  height: 100%;
	  width: 100%;
	  text-align: center;
	  line-height: 0.95rem;
	  font-weight: bold;
	  font-size: 0.32rem;
	  color: #404040;
	  letter-spacing: 5px;
	  float: right;
	  border-bottom:1px solid #eaeaea;
	}
	section{
	  width: 100%;margin-top: 0.95rem;
	}
	.denmark-div{
	  width: 100%;height: 3.02rem;
	}
	.denmark-div img{
	  width: 100%;height: 100%;
	}
	.denmarklive{
	  width: 7.02rem;margin: 0 auto
	}
	.denmarklive h3{
	  width: 100%;height: 0.45rem;
	  line-height: 0.45rem;font-size: 0.3rem;
	  color: #404040;letter-spacing: 1px;
	  text-align: center;
	}
	.denmarklive p{
	  width: 100%;text-align: left;font-size: 0.26rem;
	  color: #6A6A6A;letter-spacing: 0.4px;line-height: 0.41rem;
	}
	.denmark-ul{
	  width: 100%;
	}
	.denmark-ul li{
	  position: relative;
	  width: 3.46rem;
	  height: 4.6rem;
	  float: left;
	  margin-left: 0.2rem;
	}
	.denmark-ul li img{
	  width: 100%;height: 3.42rem;
	}
	.denmark-ul li p{
	  width: 100%;height: 0.4rem;
	  font-size: 0.24rem;color: #404040;letter-spacing: 0.17px;
	  text-align: center;
	}
	.denmark-ul li span{
	  display: inline-block;width: 100%;height: 0.4rem;position: absolute;
	  top: 4.04rem;
	  text-align: center;font-size: 0.26rem;color: #FD888B;letter-spacing: 0.19px;
	}
</style>


